import axios from "axios";
import qs from 'qs';
axios.defaults.paramsSerializer = (params) => qs.stringify(params, {encode: false});
let axiosCall = axios.CancelToken.source();

// Add a request interceptor
axios.interceptors.request.use(config => {
    console.log("------------  Ajax request", config);
    config.cancelToken = axiosCall.token;
    config.url.includes(`Error:-`) && axiosCall.cancel(config.url.split(`Error:-`)[1]);
    return config;
}, error => Promise.reject(error));

const Api = (env = "") => ({
    async get(url, args = {}) {
        return apiRequest('get', url, args);
    },

    async post(url, data, opts = {}) {
        return apiRequest('post', url, data, opts);
    },

    async put(url, data, opts = {}) {
        return apiRequest('put', url, data, opts);
    },

    async patch(url, data, opts = {}) {
        return apiRequest('patch', url, data, opts);
    },

    async delete(url, opts = {}) {
        return apiRequest('delete', url, opts);
    },
});


const apiRequest = async (method, url, args) => {
    return new Promise(async (resolve, reject) => {
        let baseUrl = localStorage.getItem('base_backend_server_domain')+"/api/"
        axios.defaults.withCredentials = true
        try {
            const data = await axios({
                url: baseUrl+url,
                method: method,
                ...args
              })

            resolve(data);

        } catch (error) {
            reject(error);
        }
    });
};



export {
    Api,
    apiRequest,
}
