import React, { useState, useEffect } from 'react';
import { Grid,Paper, Typography, Button, TextField } from "@material-ui/core";
import { useHistory } from "react-router-dom";
import ReCAPTCHA from "react-google-recaptcha";
import { Api } from '../common/api';



let AddUser = () => {

  
  const history = useHistory();
  const [firstname, setFirstname] = useState("")
  const [lastname, setLastname] = useState("") 
  const [email, setEmail] = useState("") 
  const [password, setPassword] = useState("") 

  function onRecaptchaChange(value: any) {
    console.log("Captcha value:", value);
  }

  const createUser = (e: any) =>{ 
    e.preventDefault();


    let postData = {
        "firstname": firstname,
        "lastname": lastname,
        "email": email,
        "password": password,
        "role":"client"
    }

    
    // Access FormData fields with `data.get(fieldName)`
    // For example, converting to upper case

    //http://localhost:8000/api/user/getCurrentUer/
    let url = "user/createUser"

    const createUser = async() => {
        const res =  await Api(`prod`).post(url,
            {
                "data":postData,
                "headers": {
                    'content-type': 'application/json'
                }
            },{});
            history.push("/dashboard");
    } 
    createUser();

   
  }

  return (
    <Grid container justify="center" spacing={4}  direction="row">
        <Grid item  xs={8}>
            <Typography component="h1" variant="h5"> Add User </Typography>
        </Grid>
        <Grid item xs={8}>
            <Grid container direction="column" justify="center"  className="food-diary-page">
                <Paper variant="elevation" elevation={2} className="food-diary-background">
                    <Grid item>
                        <form >
                            <Grid container direction="column" spacing={2}>
                                <Grid item xs={12} >
                                    <TextField
                                        id="first-name"
                                        label="First Name"
                                        style={{ margin: 8 }}
                                        placeholder="First Name"
                                        fullWidth
                                        margin="normal"
                                        onChange={e => setFirstname(e.target.value)}
                                        InputLabelProps={{
                                            shrink: true,
                                        }}
                                    />
                                </Grid>
                                <Grid item xs={12} >
                                    <TextField
                                        id="last-name"
                                        label="Last Name"
                                        style={{ margin: 8 }}
                                        placeholder="Last Name"
                                        fullWidth
                                        margin="normal"
                                        onChange={e => setLastname(e.target.value)}
                                        InputLabelProps={{
                                            shrink: true,
                                        }}
                                    />
                                </Grid>
                                <Grid item xs={12} >
                                    <TextField
                                        id="email"
                                        type="email"
                                        label="Email"
                                        style={{ margin: 8 }}
                                        placeholder="Email"
                                        onChange={e => setEmail(e.target.value)}
                                        fullWidth
                                        margin="normal"
                                        InputLabelProps={{
                                            shrink: true,
                                        }}
                                    />
                                </Grid>

                                <Grid item xs={12} >
                                    <TextField
                                        id="password"
                                        type="password"
                                        label="Password"
                                        onChange={e => setPassword(e.target.value)}
                                        style={{ margin: 8 }}
                                        placeholder="Password"
                                        fullWidth
                                        margin="normal"
                                        InputLabelProps={{
                                            shrink: true,
                                        }}
                                    />
                                </Grid>
                                
                                <Grid item xs={12} >
                                    <TextField
                                        id="confirm-password"
                                        type="password"
                                        label="Confirm Password"
                                        style={{ margin: 8 }}
                                        placeholder="Confirm Password"
                                        fullWidth
                                        margin="normal"
                                        InputLabelProps={{
                                            shrink: true,
                                        }}
                                    />
                                </Grid>

                                <Grid item xs={12} >
                                    <ReCAPTCHA
                                        sitekey="6LfQGdgZAAAAAI898BgNxE2_KnrYqCtAKD_QTDdA"
                                        onChange={onRecaptchaChange}
                                    />
                                </Grid>

                                <Grid item xs={12} >
                                    <Grid container style={{ paddingTop: 100 }}   justify="flex-end">
                                        <Grid item xs={2} >
                                            <Button fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={createUser}> Register </Button>
                                        </Grid>
                                    </Grid>
                                </Grid>

                            </Grid>
                        </form>
                    </Grid>
                </Paper>
            </Grid>
        </Grid>
    </Grid>
     
  );
}


export default AddUser;