
import React, { useState, useEffect } from 'react';
import  ReceipeList  from '../receipe/ReceipeList'
import MyNetwork from '../chat/MyNetwork';
import DiaryList from '../diary/DiaryList';
import getUserConfig from '../../config/UserPermissionConfig'
import Alert from '@material-ui/lab/Alert';
import { Grid, AppBar, Tabs, Tab, Box  } from "@material-ui/core";
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';


function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
}));

function Dashboard(props) {

  const classes = useStyles();
  const [value, setValue] = React.useState(0);


  const getTabIndex = (tabType) => {
    if(tabType == "diary"){
      return 0;
    }
    if(tabType == "network"){
      if(getUserConfig(localStorage.getItem('userRole')).includes("DiaryList")){
        return 1;
      }else{
        return 0;
      }
    }

    if(tabType == "receipe"){
      if(getUserConfig(localStorage.getItem('userRole')).includes("DiaryList") && getUserConfig(localStorage.getItem('userRole')).includes("MyNetwork")){
        return 2;
      }else if( getUserConfig(localStorage.getItem('userRole')).includes("MyNetwork")){
        return 1;
      }else{
        return 0;
      }
    }
  };

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  
  const [reloadGroups, setReloadGroups] = useState(0)
  const [authorizedstatus, setAuthorizedstatus] = useState(false)

  function getUrlVars() {
    var url = window.location.href;
    var vars = {};

    if(url != undefined){
      try{
        var hashes = url.split("?")[1];
        var hash = hashes.split('&');
        var params = {}
        
        for (var i = 0; i < hash.length; i++) {
          params=hash[i].split("=");
          vars[params[0]] = params[1];
        }
      }catch{

      }
     
    }
    
   
    return vars;
  }

  useEffect(() => {
    const urlParams = getUrlVars();
    if(urlParams["authorized"] != undefined && urlParams["authorized"]  == "true"){
      setAuthorizedstatus(true)
    }
    if(urlParams["tabvalue"] != undefined){
      setValue(parseInt(urlParams["tabvalue"]))
    }
  }, [reloadGroups]);

  

  return (
    <div >
      {(authorizedstatus)?
        <Grid container justify="center" spacing={4}  direction="row">
          <Grid item  xs={10}>
          <Alert severity="success">Authorisation Successful</Alert>
          </Grid>
        </Grid>
        
      :
        <div></div>
      }
      <Grid container justify="center" spacing={4}  direction="row">
        <Grid item  xs={10}>
          <AppBar position="static">
              <Tabs value={value} onChange={handleChange} aria-label="simple tabs example">
              {(getUserConfig(localStorage.getItem('userRole')).includes("DiaryList"))?
                (<Tab label="My Diary" {...a11yProps(getTabIndex("diary"))} />)
              :
                ("")
              } 
             
              {(getUserConfig(localStorage.getItem('userRole')).includes("MyNetwork"))?
                (<Tab label="My Network" {...a11yProps(getTabIndex("network"))} />)
              :
                ("")
              }

              {(getUserConfig(localStorage.getItem('userRole')).includes("ReceipeList"))?
                (<Tab label="Receipe List" {...a11yProps(getTabIndex("receipe"))} />)
              :
                ("")
              }

            </Tabs>
           

            {(getUserConfig(localStorage.getItem('userRole')).includes("DiaryList"))?
              (
                <TabPanel value={value} index={getTabIndex("diary")} style={{backgroundColor:"#fff"}}>
                  <DiaryList/>
                </TabPanel>
              )
            :
              ("")
            }

            {(getUserConfig(localStorage.getItem('userRole')).includes("MyNetwork"))?
              (
                <TabPanel value={value} index={getTabIndex("network")} style={{backgroundColor:"#fff"}}>
                  <MyNetwork/>
                </TabPanel>
              )
            :
              ("")
            }
      
            {(getUserConfig(localStorage.getItem('userRole')).includes("ReceipeList"))?
              (
                <TabPanel value={value} index={getTabIndex("receipe")} style={{backgroundColor:"#fff"}}>
                  <ReceipeList/>
                </TabPanel>
              )
            :
              ("")
            }
          </AppBar>
        </Grid>
      </Grid>


        
    </div>
  );
}

export default Dashboard;