import React, { Component } from 'react';
import { dummyUsers } from "./Users";
import { withRouter } from 'react-router-dom';
import { Grid,Paper,Typography, Button  } from "@material-ui/core";
import Rating from '@material-ui/lab/Rating';
import { Api } from '../common/api';
import defaultProfilePic from "../../assets/default-profile.jpg";
import StripeCheckout from 'react-stripe-checkout';
import Alert from '@material-ui/lab/Alert';
import { connect } from "react-redux";
import { compose } from 'redux'

class MyNetwork extends Component {
    constructor() {
        super();

        let title = "";
        let userUrl = "";

        if(localStorage.getItem('userRole') == "client"){
            title = "My Coaches";
            userUrl =  "user/?role=coach";
        }else if(localStorage.getItem('userRole') == "coach"){
            title = "My Clients";
            userUrl =  "user/?role=client";
        }else{
            title = "User List";
            userUrl =  "user/?role=admin";
        }

        this.state = {
            title: title,
            userUrl: userUrl,
            checkoutAlert: false,
            users: []
        };


       
    }



    componentDidMount() {


        let fetchuser = async() => {
            let users = await Api(`prod`).get(this.state.userUrl,{})
            this.setState({
                users : users.data.result
            });

        }
        
        fetchuser();
        this.setState({
            checkoutAlert: false
        });
        
    }



    redirectAddUser = (e) =>{ 
        this.props.history.push(`/add-user`)
    }


    addUserBtnPermission = () => {
        if(localStorage.getItem('userRole') != null){
          switch(localStorage.getItem('userRole')) {
            case "admin":  return <Button fullWidth={false}  variant="contained" color="primary" type="submit" className="button-block" onClick={this.redirectAddUser}> Add Coach </Button>;
            case "coach":  return   <div></div>;
            case "client": return    <div></div>;
            default:      return <div></div>
          }
        }
        
    }
    getCookie = (name) => {
        var cookieValue = null;
        if (document.cookie && document.cookie !== '') {
            var cookies = document.cookie.split(';');
            for (var i = 0; i < cookies.length; i++) {
                var cookie = cookies[i].trim();
                if (cookie.substring(0, name.length + 1) === (name + '=')) {
                    cookieValue = decodeURIComponent(cookie.substring(name.length + 1));
                    break;
                }
            }
        }
        return cookieValue;
    }

    
    onToken = (token,user_id,price) => {

        const sendChargeReq = async(token,user_id,price) => {
            const _csrfToken = null;

            let url = "user/csrf/"
            let tokenData = await Api(`prod`).get(url,{})

            let csrfToken = tokenData.data.csrfToken;
           
        
            await Api(`prod`).post('user/charge/',
            {
                "data":JSON.stringify({
                    token: token.id,
                    amount: price * 100,
                    description: "Coach Booking Payment",
                    person_id: user_id,
                  }),
                "headers": {
                    'content-type': 'application/json',
                    'X-CSRFToken': csrfToken
                }
            },{});
            this.setState({
                checkoutAlert: true
            });

        }
        sendChargeReq(token,user_id,price)
        

    }

    bookBtnPermission = (user_id,price) => {
        if(localStorage.getItem('userRole') != null){
          switch(localStorage.getItem('userRole')) {
            case "admin":  return <div></div>;
            case "coach":  return   <div></div>;
            case "client": return    <div>{(price)?(<span><StripeCheckout
                token={(token) => {this.onToken(token,user_id,price)}}
                stripeKey="pk_test_51HdGfLHdkoE92aVGZVZH9iX7tD4BdmZfRxq3kKBcSYqglUCtOa1Jfq6z1aXS2nV1IdUzY3JOleMArUHNnASdHXIg00kXCi6ztL"
              /></span>):(<span></span>)}</div>;
            default:      return <div></div>
          }
        }
    }

    getPriceText = (price) => {
        if(localStorage.getItem('userRole') != null){
          switch(localStorage.getItem('userRole')) {
            case "admin":  return <div></div>;
            case "coach":  return   <div></div>;
            case "client": return    <div>{(price)?(<span>${price}</span>):(<span>$0</span>)}</div>;;
            default:      return <div></div>
          }
        }
    }



   
    

    render() {
     

        return (
            <Grid container justify="center" spacing={4}  direction="row">
                <Grid item  xs={12} sm={12} md={12} >
                    <Typography component="h1" variant="h5"> {this.state.title} </Typography>
                </Grid>
                <Grid item   xs={12} sm={12} md={12}>
                    {this.addUserBtnPermission()}
                
                </Grid>
                <Grid item   xs={12} sm={12} md={12}>
                    {(this.state.checkoutAlert)?
                        <Alert severity="success">Checkout Successful</Alert>
                    :
                        <div></div>
                    }
                    
                </Grid>
                
                <Grid item    xs={12} sm={12} md={12} className="food-diary-page">
                    <Paper variant="elevation" elevation={2} className="food-diary-background" >
                        <div className="users">
                    

                            <div className="users-container"> 
                                <ul>
                                    { this.state.users.map(user => 
                                        <li key={user.id} className="user">
                                            <Grid container justify="center" spacing={4}  direction="row">
                                                <Grid item  xs={12} sm={12} md={2} >
                                                    <picture className="user-picture">
                                                        {(user.profilepic != "" && user.profilepic != null)?(
                                                            <img src={`${localStorage.getItem('base_backend_server_domain')}/media/${user.profilepic}`} alt={`${user.email}`} />
                                                        ):(
                                                            <img src={defaultProfilePic} alt={`${user.email}`} />
                                                        )
                                                        }
                                                
                                                    </picture>
                                                </Grid>
                                                <Grid item  xs={12} sm={12} md={3} >
                                                    <div className="user-info">
                                                        <h4>{user.email}</h4>
                                                    </div>
                                                </Grid>
                                                <Grid item  xs={12} sm={12} md={1} >
                                                    {this.getPriceText(user.price)}
                                                </Grid>
                                                <Grid item  xs={12} sm={12} md={2} >
                                                    {this.bookBtnPermission(user.id,user.price)}
                                                </Grid>
                                                <Grid item  xs={12} sm={12} md={2} >
                                                    <Rating
                                                        style = {{alignItems: "center", justifyContent: "center"}}
                                                        name="hover-feedback"
                                                        value={2}
                                                        precision={0.5}
                                                        onChange={(event, newValue) => {
                                                            
                                                        }}
                                                        onChangeActive={(event, newHover) => {
                                                            
                                                        }}
                                                    />
                                                </Grid>
                                                <Grid item  xs={12} sm={12} md={2} className="user-action">
                                                    <button onClick={()=>{this.props.history.push('/chatroom')}}>Message </button>
                                                </Grid>
                                            </Grid>
                                            
                                           
                                          
                                        </li>
                                    )}
                                </ul>

                                <div className="chatbox-container" ref={c => this.container = c}>
                                    <div id="talkjs-container" style={{height: "300px"}}><i></i></div>
                                </div>
                            </div>
                        </div>
                    </Paper>
                </Grid>
            </Grid>
        )
    }
}

const mapStateToProps = state => ({
    loginSuccessResponse: state.auth.loginSuccessResponse
});


export default connect(mapStateToProps)(withRouter(MyNetwork));
   
