import React from 'react';
import { BrowserRouter, Switch, Route, NavLink, HashRouter } from 'react-router-dom';
import Login from './components/login/Login';
import Dashboard from './components/dashboard/Dashboard';
import Home from './components/home/Home';
import Logout from './components/logout/Logout';
import AddUser from './components/user/AddUser';
import AddReceipe from './components/receipe/AddReceipe';
import AddDiary from './components/diary/AddDiary';
import MyNetwork from './components/chat/MyNetwork';
import Chatroom from './components/chat/Chatroom';
import PrivateRoute from './components/common/privateroute';
import EditProfile from './components/user/EditProfile';
import Checkout from './components/stripe/Checkout';
import './App.css';



function App() {

  return (
    <div className="App">
      <BrowserRouter >
      <HashRouter>
        <div>
      
          <div className="content">
            <Switch>
              <Route exact path="/" component={Login} />
              <Route exact path="/login" component={Login} />
              <Route path="/home" component={Home} />
              <PrivateRoute path="/dashboard" component={Dashboard} />
              <PrivateRoute path="/logout" component={Logout} />
              <PrivateRoute path="/add-user" component={AddUser} />
              <PrivateRoute path="/add-receipe" component={AddReceipe} />
              <PrivateRoute path="/add-diary" component={AddDiary} />
 
              <PrivateRoute path="/mynetwork" component={MyNetwork}/>
              <PrivateRoute path="/chatroom" component={Chatroom}/>
              <PrivateRoute path="/edit-profile-coach" component={EditProfile}/>
              <PrivateRoute path="/receipe/update/:id" component={AddReceipe}/>
              <PrivateRoute path="/diary/update/:id" component={AddDiary}/>
              <PrivateRoute path="/checkout" component={Checkout}/>
            </Switch>
          </div>
        </div>
      </HashRouter>
      </BrowserRouter>
    </div>
  );
}



export default App;