import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {initiateLogin} from '../../actions/authActions'
import { connect } from 'react-redux';
import Alert from '@material-ui/lab/Alert';

import {
  Button,
  TextField,
  Grid,
  Paper,
  Typography,
  Link,
  } from "@material-ui/core";

const useStyles = makeStyles({
  gridContainer: {
    marginTop: 200,
  },

  elementStyle: {
    marginTop: 10,
    marginBottom: 10,
  }
 
});

const Login = (props) => {
  const classes = useStyles(); 
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [reloadGroups, setReloadGroups] = useState(0)
  const { initiateLogin, history } = props;

  // handle button click of login form
  const handleLogin = (e) => {
    e.preventDefault();
    initiateLogin({email,password,history });
  }

  useEffect(() => {
    if(localStorage.getItem('isAuthenticated')){
      history.push("/dashboard");
    }
    localStorage.setItem('base_backend_server_domain','http://python.food-diary.com')
    
  }, [reloadGroups]);

  return (
    <div>

      <Grid container spacing={0} justify="center" direction="column"  className={classes.gridContainer}>
        <Grid item>
          
          <Grid container direction="row" justify="center"   className="food-diary-page">
          
            <Paper variant="elevation" elevation={2} className="food-diary-background">
              { localStorage.getItem('authenticationFailed') == "true"  ?
                  <Grid item  >
                    <Alert severity="error">Login Failed</Alert>
                    { localStorage.setItem('authenticationFailed',"false") }
                  </Grid>
                  
                  :
                  localStorage.setItem('authenticationFailed',"false")
                  
              }

              
              <Grid item className={classes.elementStyle} >
                <Typography component="h1" variant="h5"> Sign in </Typography>
              </Grid>
              

              
            
              
              <Grid item>
                <form onSubmit={handleLogin}>
                  <Grid container direction="column" spacing={2}>
                    <Grid item>
                      <TextField type="email" placeholder="Email" fullWidth name="username" variant="outlined" value={email} onChange={e => setEmail(e.target.value)} required autoFocus /> </Grid>
                    <Grid item>
                      <TextField type="password" placeholder="Password" fullWidth name="password" variant="outlined" value={password} onChange={e => setPassword(e.target.value)} required /> </Grid>
                    <Grid item>
                      <Button variant="contained" color="primary" type="submit" className="button-block"> Submit </Button>
                    </Grid>
                  </Grid>
                </form>
              </Grid>
              <Grid item className={classes.elementStyle}>
                <Link href="#/add-user" variant="body2"> Register </Link>
              </Grid>
            </Paper>
          </Grid>
        </Grid>
      </Grid>
    </div>

  );
}



const mapStateToProps = state => ({  })
export default connect(mapStateToProps, { initiateLogin })(Login);