import React from 'react';
import { makeStyles, Typography } from '@material-ui/core';
import { TreeView, TreeItem } from '@material-ui/lab';
import { ArrowDropDown, ArrowRight } from '@material-ui/icons';
import { withRouter } from "react-router-dom";
import dashboardIcon from "../../assets/icon-analytics.svg";
import fleetIcon from "../../assets/four-rectangles.svg";
import eventsIcon from "../../assets/Calendar.svg";
import systemIcon from "../../assets/cog-wheel.svg";

import { connect } from "react-redux";
import reportsIcon from "../../assets/icon-reports.svg";
import Tooltip from "./Tooltip"

const useTreeItemStyles = makeStyles(theme => ({
    root: {
        paddingTop: theme.spacing(2),
        backgroundColor: "#FBFBFB",
        '&:focus > $content': {
            backgroundColor: "#83C4C420",
        },
    },
    content: {
        color: "#83C4C4",
        display: "block",
        '&:hover': {
            backgroundColor: "#83C4C410",
        }
    },
    expanded: {},
    group: {
        marginLeft: 0,
        '& $content': {
            paddingLeft: theme.spacing(2),
        },
    },
    labelRoot: {
        display: 'flex',
        alignItems: 'center',
        padding: theme.spacing(1, 0),
    },
    labelIcon: {
        marginRight: theme.spacing(3.3),
    },
    labelText: {
        fontSize: '1.235vw'
    }
}));

const useStyles = makeStyles({
    root: {
        height: 264,
        flexGrow: 1,
        maxWidth: 400,
    },
});

const LeftSideNav = props => {
    const classes = useStyles();
    const treeItemClasses = useTreeItemStyles();
    const { auth: { role = "" } = {} } = props;
    const getModule = role => {
        switch (role) {
            case 1: return "ausgrid";
            case 2: return "ausgrid";
            case 3: return "powershop";
            default: return null;
        }
    }
    // const getFleet = (fleetTree = []) => {
    //     return fleetTree.map(fleet => {
    //         fleet.icon = fleetIcon;
    //         fleet.component = "Fleet";
    //         fleet.text = (fleet.fleet_name && "Fleet") || fleet.group_name || fleet.sn || "";
    //         fleet.onClick = "/fleet";
    //         fleet.children = (
    //             ((Array.isArray(fleet.Group) && fleet.Group.length) && getFleet(fleet.Group)) ||
    //             ((Array.isArray(fleet.Systems) && fleet.Systems.length) && getFleet(fleet.Systems))
    //         ) || [];
    //         return fleet;
    //     })
    // }
    // const getRoleBasedFleet = () => getFleet(fleetTree).filter(f => f.fleet_name && f.fleet_name.toLowerCase().includes(getModule(role))).shift();
    const items = [
        { icon: fleetIcon, component: "Dashboard", text: "Dashboard", onClick: "/dashboard" },
        { icon: fleetIcon, component: "Chatroom", text: "Chat Room", onClick: "/chatroom" },
    ];

    const getTreeItem = (treeItems = []) => {

        return treeItems.map((treeItem, key) => {
            let children = undefined;
            if (Array.isArray(treeItem.children) && treeItem.children.length) {
                children = getTreeItem(treeItem.children)
            }
            return (<TreeItem
                nodeId={treeItem.text}
                classes={{
                    root: treeItemClasses.root,
                    content: treeItemClasses.content,
                    group: treeItemClasses.group,
                    expanded: treeItemClasses.expanded,
                }}
                label={
                    <div className={treeItemClasses.labelRoot}>
                        <figure style={{ display: "flex" }}>
                            <Tooltip title={treeItem.text} placement="left" >
                                <img className={treeItemClasses.labelIcon} src={treeItem.icon} />
                            </Tooltip>
                            <figcaption className={treeItemClasses.labelText}> {treeItem.text}</figcaption>
                        </figure>
                    </div>
                }
                children={children}
                onClick={() => props.history.push(treeItem.onClick)}
            />
            );
        });
    }

    return (
        <TreeView
            className={classes.root}
            defaultCollapseIcon={<ArrowDropDown />}
            defaultExpandIcon={<ArrowRight />}
            defaultEndIcon={<div style={{ width: 24 }} />}>
            {getTreeItem(items)}
        </TreeView>
    );
}

const mapStateToProps = state => ({ auth: state.auth })
export default connect(mapStateToProps)(withRouter(LeftSideNav));

