import { put, takeLatest } from 'redux-saga/effects';
import { Api } from '../components/common/api';
import { Base64 } from 'js-base64';
import * as actionTypes from '../actions/types';




function* login(action) {
    const { payload: { email, password, history } } = action;
    try {
        let args= {
            withCredentials: true,
            headers: {
                "Authorization": "Basic " + Base64.encode(`${email}:${password}`),
                "Content-Type": "application/json"
            }
        };
        let method = "get";
        let url = "user/getCurrentUer/"




        const res = yield Api(`prod`).get(url,  args);
        const payload = {"loginSuccessResponse":res,"history":history};

        yield put({ type: "LOGIN_SUCCESS", payload });

    } catch (error) {
        localStorage.setItem('isAuthenticated', false);
        localStorage.setItem('authenticationFailed', true);
        yield history.push('/login') //will remove it
    }
}

function* loginSuccess(action) {
    const { payload: { loginSuccessResponse, history } } = action;
    let role;
    if(loginSuccessResponse.data.is_user == true && loginSuccessResponse.data.is_coach == true){
        role = "admin";
    }else if(loginSuccessResponse.data.is_user == false && loginSuccessResponse.data.is_coach == true){
        role = "coach";
    }else{
        role = "client";
    }
    localStorage.setItem('isAuthenticated', true);
    localStorage.setItem('userRole', role);

    
    yield history.push('/dashboard')
    
    
}



export const authSagas = [
    takeLatest(actionTypes.INITIATE_LOGIN, login),
    takeLatest(actionTypes.LOGIN_SUCCESS, loginSuccess),
]